<template>
    <div class="vue-template">
        <form @submit.prevent="userLogin">
            <h3>Ingresar</h3>

            <div class="form-group">
                <label>Email</label>
                <input type="email" class="form-control form-control-lg" v-model="user.email" />
            </div>

            <div class="form-group">
                <label>Clave</label>
                <input type="password" class="form-control form-control-lg" v-model="user.password" />
            </div>

            <button type="submit" class="btn btn-dark btn-lg btn-block">Iniciar</button>

            <p class="forgot-password text-right mt-2 mb-4">
                <router-link to="/forgot-password">¿ Se te olvidó tu contraseña ?</router-link>
            </p>
        </form>
    </div>
</template>


<script>
import firebase from "firebase";

export default {
  name: "Login",
  data() {
    return {
      user: {   
        email: '',
        password: ''
      }
    };
  },
  methods: {
    userLogin() {
        firebase
        .auth()
        .signInWithEmailAndPassword(this.user.email, this.user.password)
        .then(() => {
            this.$router.push('/dashboard')   //replace hace lo mismo pero router te deja historial
        })
        .catch((error) => {
          alert(error.message);
        });
    }
  }
};
</script>

